import React, { PropTypes, Component } from 'react';
import classNames from 'classnames';
import styles from './Navigation.css';
import withStyles from '../../decorators/withStyles';
import Link from '../Link';

@withStyles(styles)
class Navigation extends Component {

  static propTypes = {
    className: PropTypes.string,
  };

  render() {
    return (
      <div className={classNames(this.props.className, 'Navigation')} role="navigation">
        <a className="button" href="/" onClick={Link.handleClick}>Startsida</a>
        <a className="button" href="/lokaler" onClick={Link.handleClick}>Lokaler</a>
        <a className="button" href="/medlemmar" onClick={Link.handleClick}>Medlemmar</a>
        <a className="button" href="/om" onClick={Link.handleClick}>Om</a>
        <a className="button" href="/kontakt" onClick={Link.handleClick}>Kontakt</a>
      </div>
    );
  }
}

export default Navigation;



